const NotFoundPage = Containers.NotFoundPage;

let routes = [
  {
    path: "/",
    component: Dashboard,
    exact: true
  },
  {
    path: "/encode-member/:ticket_id/:client_id",
    component: EncodeMember
  },
  {
    path: "/verify-member/:ticket_id/:member_upload_id",
    component: VerifyMember
  },
  {
    path: "/view-member/:member_id",
    component: ViewMemberPage
  },
  {
	  path: '/members',
	  component: MemberPage
  },
  {
    path: "/dashboard",
    component: Dashboard
  },
];
const prefix = '/membership';
routes.map((route)=>route.path = `${prefix}${route.path}`);

export default function App() {
  return (
    <ThemeProvider theme={theme}>
    	<Helmet
        	titleTemplate="NEO - %s"
        	defaultTitle="NEO"
      	>
        	<meta name="description" content="NEO application" />
      	</Helmet>
		<Components.UI.PageLayout>
			<Switch>
				{renderRoutes(routes)}
				<Route path="" component={NotFoundPage} />
			</Switch>
		</Components.UI.PageLayout>
    </ThemeProvider>
  );
}
