import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Dialog, DialogTitle, DialogContent,
        DialogActions, Grid, Button, InputLabel,
        TextField, FormHelperText} from '@material-ui/core/';
import { useState } from 'react';


interface AddNewBatchModalProps {
    isModalOpen: boolean;
    id: string;
    batchnames: any;
    onClose: () => void;
    onAdd: (value: String) => void;
}

const useStyles = makeStyles(theme => ({
    root: {
        flexWrap:'wrap'
    },
    dialog:{
        align:'center',
        justify:'center',
        width:'95%',
        fontFamily: 'usual',
    },
    dialogContent :{
        paddingTop : '0px',
        marginBottom: '25px'
    },
    dialogTitle: {
        paddingTop: theme.spacing(5),
    },
    dialogFirstTitle:{
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600,
        marginBottom: '5px'
    },
    dialogAction: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2)
    },
    cancelButton: {
        width:100,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: "#FFFFFF"
    },
    addButton: {
        minWidth:100
    },
    label: {
        fontSize:'13px',
        fontWeight: 400
    },
    batchName:{
        minWidth: '280px'
    },
    errorText:{
        marginTop: '5px',
        color: "#E53935"
    }
}));
  

export const AddNewBatchModal: React.FC<AddNewBatchModalProps> = (
	props: AddNewBatchModalProps,
): JSX.Element => {
    const {
        isModalOpen,
        id
    } = props;
    const classes = useStyles();
    const [ batchname, setBatchName] = useState("");
    const [ errorFlag, setErrorFlag] = useState(false);
    const [ errorMessage, setErrorMessage] = useState("Name is required");

    function handleChangeName(e: React.ChangeEvent<HTMLInputElement>){
        const { value } = e.target;
        setBatchName(value);
        setErrorFlag(false);
    }

    function handleAdd(){
        if(batchname){
            if(props.batchnames){
                for(var i in props.batchnames){
                    if(props.batchnames[i]["label"] 
                        && props.batchnames[i]["label"].toLowerCase() === batchname.toLowerCase()){
                            setErrorFlag(true);
                            setErrorMessage("Name already exist")
                            return;
                        break;
                    }
                }
                props.onAdd(batchname);
            }else{
                props.onAdd(batchname);
            }
        }else{
            setErrorFlag(true);
            setErrorMessage("Name is required")
        }
    }

    return (
        <Dialog
            id={id}
            maxWidth={false}
            open={isModalOpen}
            scroll={"paper"}
            aria-labelledby="modal-title"
            className={classes.dialog}
            style={{zIndex: 9999}}
        >
            <DialogTitle className={classes.dialogTitle} disableTypography={true} id={ id+ "_title"} >
                <div className={classes.dialogFirstTitle}>
                    Add Batch Name
                </div>
            </DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <InputLabel
                    className={classes.label}
                    htmlFor="add_batch_name">
                    Name
                </InputLabel>
                <TextField
                    style={{marginTop:'8px', width: '100%'}}
                    className={classes.batchName}
                    id="add_new_batch_name"
                    margin="normal"
                    variant="outlined"
                    value={batchname}
                    onChange={handleChangeName}
                    error={errorFlag}
                    inputProps={{ 'aria-label': 'bare' }}
                    aria-describedby="component-error-text"
                />
                { errorFlag === true ?
                    (<FormHelperText 
                        id="add_new_batch_error_text"
                        className={classes.errorText}
                    >
                        {errorMessage}
                    </FormHelperText>):null
                }
            </DialogContent>
            <DialogActions className={classes.dialogAction}>
                <Grid 
                    container
                    direction="row"
                    justify="space-evenly"
                    alignItems="stretch"
                >
                    <Grid item xs={6} style={{textAlign:'center'}}>
                        <Button
                            id={"add_new_batch_cancel_btn"}
                            className={classes.cancelButton}
                            variant="contained"
                            color="primary"
                            onClick={props.onClose}>
                            Cancel
                        </Button>
                    </Grid>
                    <Grid item xs={6} style={{textAlign:'center'}}>
                        <Button
                            id={"add_new_batch_add_btn"}
                            className={classes.addButton}
                            variant="contained"
                            color="primary"
                            onClick={handleAdd}>
                            Add
                        </Button>
                    </Grid>
                </Grid>
            </DialogActions>   
        </Dialog>
    )
}

AddNewBatchModal.defaultProps = {
    isModalOpen: false,
    id: 'modal_id',
};
