
let API_URL: string;
if(process.env.NODE_ENV === "development"){
    API_URL = "https://neo-local.seerlabs.com/api";
}else{
    API_URL = "http://18.138.94.224:4101/api";
}

// const PROCESSMAKER_TOKEN = "db649586ddbe061e9e4a7c8320ed0948ffccc4a4";
const PROCESSMAKER_TOKEN = localStorage.getItem('pm_token');

export class API {
    static getTicketData(ticket_id: any){
        const TICKET_URL = API_URL+'/tickets/'+ticket_id;
        return fetch(TICKET_URL, {
		   	method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static getClientsData(){
        const CLIENT_URL = API_URL+'/clients/';
        return fetch(CLIENT_URL, {
		   	method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static getClientContracts(client_id: any){
        let CONTRACT_URL = API_URL+'/clients/contracts';
        if(client_id){
            const filter = {
                where:{
                    _id: client_id
                }
            }
            CONTRACT_URL = CONTRACT_URL + '?filter=' + JSON.stringify(filter);
        }
        return fetch(CONTRACT_URL, {
		   	method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static getClientDataMap(client_id: string){
        let DATAMAP_URL = API_URL+'/members/data-mapping';
        if(client_id){
            const filter = {
                where:{
                    client_id: client_id
                }
            }
            DATAMAP_URL = DATAMAP_URL+'?filter='+JSON.stringify(filter);
        }
        return fetch(DATAMAP_URL, {
		   	method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static postNewDataMap(payload: any){
        let DATAMAP_URL = API_URL+'/members/data-mapping';
        return fetch(DATAMAP_URL, {
            method: 'post',
            headers: {'Content-Type':'application/json'},
            body: JSON.stringify(payload)
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static postUploadMember(payload: string){
        let MEMBER_UPLOAD_URL = API_URL+'/clients/member-upload/contract?pmaker_token='+PROCESSMAKER_TOKEN;
        return fetch(MEMBER_UPLOAD_URL, {
            method: 'post',
            headers: {'Content-Type':'application/json'},
            body: JSON.stringify(payload)
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static getUploadMember(id: string){
        let MEMBER_UPLOAD_URL = API_URL+'/member-upload/'+id;
        return fetch(MEMBER_UPLOAD_URL, {
            method: 'get',
            headers: {'Content-Type':'application/json'},
        }).then(response => response.json()
        ).catch(error => {
            console.log('client error: ', error);
        });
    }

    static patchUploadMember(id: string, data: any){
        let MEMBER_UPLOAD_URL = API_URL+'/member-upload/'+id;
        return fetch(MEMBER_UPLOAD_URL, {
            method: 'PATCH',
            headers: {'Content-Type':'application/json'},
            body: JSON.stringify(data)
        }).then(response => response.json()
        ).catch(error => {
            console.log('client error: ', error);
        });
    }

    static sendEmailReport(email: string[]){
        // let EMAIL_REPORT_URL = API_URL+'/';

        console.log('sendEmailReport emails: ', email);

        return 'response';
    }

    static getUploadMemberList(){
        let MEMBER_UPLOAD_URL = API_URL+'/member-upload';
        return fetch(MEMBER_UPLOAD_URL, {
            method: 'get',
            headers: {'Content-Type':'application/json'},
        }).then(response => response.json()
        ).catch(error => {
            console.log('client error: ', error);
        });
    }

    static getMemberData(member_id: string){
        let GET_MEMBER_URL = API_URL+'/members/'+member_id;
        
        return fetch(GET_MEMBER_URL, {
            method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
        });
    }
    
    static getMembers(){
        const CLIENT_URL = API_URL+'/members/';
        return fetch(CLIENT_URL, {
		   	method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static getMemberDocuments(member_id: string){
        const MEMBER_DOCUMENTS = API_URL+'/members/documents/'+member_id;
        return fetch(MEMBER_DOCUMENTS, {
		   	method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static putMemberDocuments(payload: any, member_id:string){
        let MEMBER_DOCUMENTS = API_URL+'/members/documents/'+member_id;
        return fetch(MEMBER_DOCUMENTS, {
            method: 'put',
            headers: {'Content-Type':'application/json'},
            body: JSON.stringify(payload)
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }
}