import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Dialog, DialogTitle, DialogContent,
    DialogContentText, DialogActions, Grid,
    Button, TextField, InputLabel, FormGroup,
    FormControlLabel, Checkbox, FormHelperText} from '@material-ui/core/';
import { useState } from 'react';

import { TableSelect } from '../Table';
import '../../style.scss';

interface AddNewDataMapModalProps {
    isModalOpen: boolean;
    id: string;
    title: string;
    data: any;
    onClose: () => void;
    setDataValues: (data: any, attr: string) => void;
    savingNewDataMap: (payload: any) => void;
}

const useStyles = makeStyles(theme => ({
    root: {
        flexWrap:'wrap'
    },
    dialog:{
        align:'center',
        justify:'center',
        width:'100%',
        fontFamily: 'usual',
    },
    dialogContent :{
        paddingTop : '0px',
    },
    dialogContentText :{
        fontSize: '0.875rem'
    },
    container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        minWidth: '250px'
    },
    dialogTitle: {
        marginTop: theme.spacing(2),
        marginBottom: '5px',
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 600,
        padding: '16px 18px',
    },
    dialogAction: {
        marginBottom: theme.spacing(1),
        justifyContent:'center',
        padding: '3px'
    },
    dialogContentContainer: {
        width: '100%'
    },
    cancelButton: {
        width:115,
        marginRight:25,
        border: '1px solid #3AB77D',
        color: '#3AB77D',
        backgroundColor: "#FFFFFF"
    },
    saveButton: {
        width:115,
        marginLeft:25,
    },
    label: {
        fontSize:'1rem'
    },
    errorText:{
        marginTop: '0px',
        marginBottom: '5px',
        color: "#E53935"
    },
    errorText2:{
        marginTop: '10px',
        color: "#E53935"
    },
}));
  

export const AddNewDataMapModal: React.FC<AddNewDataMapModalProps> = (
	props: AddNewDataMapModalProps,
): JSX.Element => {
    const {
        isModalOpen,
        id
    } = props;
    const classes = useStyles();
    const [ name, setName ] = useState("")
    const [ is_default, setIsDefault ] = useState(false);
    const [ error_name, setErrorName ] = useState(false);
    const [ error_name_message, setErrorNameMessage ] = useState("");
    const [ mappingError, setMappingError ] = useState(false);
    const [ mapping_error_message, setMappingErrorMessage ] = useState("");

    function handleValuesChange(value:string, row: string, table_name: string){
        const temp_table: any = props.data;
        temp_table["rows"][parseInt(row)]["sheet_name"] = value;
        props.setDataValues(temp_table, table_name);
        setMappingError(false);
        setMappingErrorMessage("");
    }

    function handleName(e: React.ChangeEvent<HTMLInputElement>){
        const { value } = e.target;
        setName(value);
        setErrorName(false);
        setErrorNameMessage("")
    }

    function handleDefault(e: React.ChangeEvent<HTMLInputElement>){
        setIsDefault(e.target.checked)
    }

    function onSave(){
        /******* validate name *****/
        let error_cnt = 0;
        if(name === "" || name === undefined){
            setErrorName(true);
            setErrorNameMessage("Name is required.")
            error_cnt = error_cnt + 1;
        }
        /****** validate mapping *****/
        let mapping_flag = false;
        for(const row_cnt in props.data["rows"]){
            const row = props.data["rows"][row_cnt];
            if(row["sheet_name"] !== "" && row["sheet_name"] !== undefined){
                mapping_flag = true;
                break;
            }
        }
        if(!mapping_flag){
            error_cnt = error_cnt + 1;
            setMappingError(true);
            setMappingErrorMessage("Please set mapping.");
        }

        if(error_cnt === 0){
            props.onClose();
            let payload: any = {
                client_id: "",
                name: name,
                is_default: is_default,
                mapping: []
            }
            for(const row_cnt in props.data["rows"]){
                const row = props.data["rows"][row_cnt];
                if(row["sheet_name"] !== "" && row["sheet_name"] !== undefined){
                    payload["mapping"].push({
                        system_name: row["system_name"],
                        sheet_name: row["sheet_name"]
                    })
                }
            }
            props.savingNewDataMap(payload);
        }
    }

    return (
        <Dialog
            id={id}
            open={isModalOpen}
            aria-labelledby="modal-title"
            className={classes.dialog}
        >
            <div>
                <Grid container className={classes.container} justify="flex-start" alignItems="flex-start" direction="column">
                    <Grid item xs>
                        <DialogTitle className={classes.dialogTitle} disableTypography={true} id="modal-title" >
                            {props.title}
                        </DialogTitle>
                    </Grid>
                    <Grid item xs className={classes.dialogContentContainer}>
                        <DialogContent className={classes.dialogContent} style={{padding: '0px 20px 10px 20px'}}>
                            <DialogContentText className={classes.dialogContentText}>
                                <Grid container>
                                    <Grid item xs={12}>
                                        <InputLabel
                                            className={classes.label}
                                            htmlFor="add_new_data_map_name">
                                            Name
                                        </InputLabel>
                                        <TextField
                                            style={{marginTop:'8px', width: '100%'}}
                                            id="add_new_data_map_name"
                                            margin="normal"
                                            variant="outlined"
                                            value={name}
                                            onChange={handleName}
                                            error={error_name}
                                            inputProps={{ 'aria-label': 'bare' }}
                                        />
                                        { error_name === true ?
                                            (<FormHelperText 
                                                id="add_new_data_map_name_error"
                                                className={classes.errorText}
                                            >
                                                {error_name_message}
                                            </FormHelperText>):null
                                        }
                                    </Grid>
                                    <Grid item xs={12}>
                                        <TableSelect
                                            id="add_new_data_map_table"
                                            rows={Object.keys(props.data).length > 0 ? props.data['rows']: []}
                                            columns={Object.keys(props.data).length > 0 ? props.data['columns']: []}
                                            empty_message={"No data available."}
                                            name={"add_new_data_map"}
                                            handleChange={handleValuesChange}
                                            maxHeight={'300px'}
                                        />
                                        { mappingError === true ?
                                            (<FormHelperText 
                                                id="add_new_data_map_name_error"
                                                className={classes.errorText2}
                                            >
                                                {mapping_error_message}
                                            </FormHelperText>):null
                                        }
                                    </Grid>
                                    <Grid item xs={12} style={{paddingTop: '5px'}}>
                                        <FormGroup>
                                            <FormControlLabel
                                                control={<Checkbox checked={is_default} style={{padding:'5px'}} id="add_new_data_map_set_default" color="primary" onChange={handleDefault}/>}
                                                label="Set as template default for JFC Corporation"
                                            />
                                        </FormGroup>
                                    </Grid>
                                </Grid>
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions className={classes.dialogAction}>
                            <Button id="add_new_data_map_cancel_btn" className={classes.cancelButton} variant="contained" color="secondary" onClick={props.onClose}>
                                Cancel
                            </Button>
                            <Button id="add_new_data_map_save_btn" className={classes.saveButton} variant="contained" color="primary" onClick={onSave}>
                                Save
                            </Button>
                        </DialogActions>
                    </Grid>
                </Grid>
            </div>
        </Dialog>
    )
}

AddNewDataMapModal.defaultProps = {
    isModalOpen: false,
    id: 'modal_id'
};
