import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faFileUpload,
    faTimesCircle,
    faFile
} from '@fortawesome/free-solid-svg-icons';
// import { useState } from 'react';

// import { InformationLabel } from 'Components/UI/InformationLabel';

