interface TableComponentProps {
	rows: any[];
	columns: any[];
	columnFilteringEnabled?: boolean;
	columnExtensions?: any[];
	filterExtensions?: any[];
	formattedColumns?: any;
	columnBands?: any[] | null;
	singleSelect?: boolean;
	disableSearch?: boolean;
	disablePaging?: boolean;
	disableSelect?: boolean;
	disableFilter?: boolean;
	disableSort?: boolean;
	showSelectionColumn?: boolean;
	toolbarType?: string | null;
	id: string;
	message:string;
	defaultFilter?: any[];
	onClickRow: (row: number[])=> void;
	onSelectedRadio?: (row: number, value: string)=> void;
	onFilterChange?: (response: any)=> void;
	onSortingChange?: (response: any)=> void;
	onClickCell?: (row: any) => void;
	defaultSort?: any[];
}

export const TableComponent: React.FC<TableComponentProps> = (
	props: TableComponentProps,
): JSX.Element => {
	const {
		rows,
		columns,
		formattedColumns,
		singleSelect,
		disablePaging,
		disableSearch,
		disableSelect,
		disableSort,
		disableFilter,
		toolbarType,
		columnExtensions,
		columnBands,
		id,
		showSelectionColumn,
	} = props;
	const [selection, setSelection] = useState<number[]>([]);
	const [currentPage, setCurrentPage] = useState(0);
	const [pageSize, setPageSize] = useState(5);
	const [pageSizes] = useState([5, 10, 15, 0]);

	const dataTypeProviders: any[] = [];

	function handleSelectionChange(newSelection: any[]) {
		console.log("here click row")
		if(props.onClickRow){
			// selectedRow = newSelection[0];
			if(!radioSelected && !clickCell) {
				console.log('onclickrow');
				props.onClickRow(newSelection);
			} else if(radioSelected){
				console.log('radioselected',newSelection);
				if(props.onSelectedRadio) props.onSelectedRadio(newSelection[0],selectedValue);
				radioSelected = false;
			}else if(clickCell){
				console.log(" here !!! >>>", props.onClickCell)
				if(props.onClickCell) props.onClickCell(clickValue)
				clickCell = false;
				clickValue={};
			}
		}else{
			if (singleSelect) {
				const lastSelected = newSelection.find(
					(selected: number) => selection.indexOf(selected) === -1,
				);

				console.log(newSelection, selection, lastSelected);
				if (lastSelected !== undefined) {
					setSelection([lastSelected]);
				}
			} else {
				setSelection(newSelection);
			}
		}
	}
	if (formattedColumns !== undefined && Object.keys(formattedColumns).length > 0) {
		if (formattedColumns['number'] !== undefined) {
			dataTypeProviders.push(<NumberDataTypeProvider for={formattedColumns.number} />);
		}
		if (formattedColumns['text'] !== undefined) {
			dataTypeProviders.push(<TextDataTypeProvider for={formattedColumns.text} />);
		}
		if (formattedColumns['system_name'] !== undefined) {
			dataTypeProviders.push(<SystemNameDataTypeProvider for={formattedColumns.system_name} />);
		}
		if (formattedColumns['data_name'] !== undefined) {
			dataTypeProviders.push(<DataDataTypeProvider for={formattedColumns.data_name} />);
		}
		if (formattedColumns['value_name'] !== undefined) {
			dataTypeProviders.push(<ValuesNameDataTypeProvider for={formattedColumns.value_name} />);
		}
		if(formattedColumns['required_columns'] !== undefined){
			dataTypeProviders.push(<EmptyCellDataTypeProvider for={formattedColumns.required_columns} />)
		}
		if(formattedColumns['radio'] !== undefined){
			dataTypeProviders.push(<RadioDataTypeProvider for={formattedColumns.radio} />)
		}
		if(formattedColumns['incomplete_radio'] !== undefined){
			dataTypeProviders.push(<IncompleteRadioDataTypeProvider for={formattedColumns.incomplete_radio} />)
		}
		if(formattedColumns['disapprove_action'] !== undefined){
			dataTypeProviders.push(<DisapproveRadioDataTypeProvider for={formattedColumns.disapprove_action} />)
		}
		if(formattedColumns['required_checkbox'] !== undefined){
			dataTypeProviders.push(<RequiredCheckboxDataTypeProvider for={formattedColumns.required_checkbox} />)
		}
		if(formattedColumns['upload_file'] !== undefined){
			dataTypeProviders.push(<UploadFileDataTypeProvider for={formattedColumns.upload_file} {...props} />)
		}
		if(formattedColumns['required_cell'] !== undefined){
			dataTypeProviders.push(<RequiredDataTypeProvider for={formattedColumns.required_cell} {...props} />)
		}
		
	}

	let toolbar = DefaultTableToolbar;

	return (
		<Paper id={id}>
			<Grid rows={rows} columns={columns}>
				{dataTypeProviders}
				{/* Need to initialize paging state before paging panel */}
				{!disablePaging ? (
					<PagingState
						currentPage={currentPage}
						onCurrentPageChange={setCurrentPage}
						pageSize={pageSize}
						onPageSizeChange={setPageSize}
					/>
				) : null}

				
				
				{!disablePaging ? <IntegratedPaging /> : null}
				

				
				{!disableSearch ? <SearchState /> : null}

				{!disableSort  && props.defaultSort !== undefined ? 
					(
						<SortingState defaultSorting={[]}
						sorting={props.defaultSort !== undefined ? props.defaultSort: []}
						onSortingChange={(e) => {
							console.log('onSortingChange');
							if(props.onSortingChange) {
								props.onSortingChange(e);
							}
							if(props.onFilterChange){
								props.onFilterChange(props.defaultFilter);
							}
						}}
					/>
					) : !disableSort ? (
						<SortingState defaultSorting={[]}
							onSortingChange={(e) => {
								console.log('onSortingChange');
								if(props.onSortingChange) {
									props.onSortingChange(e);
								}
								if(props.onFilterChange){
									props.onFilterChange(props.defaultFilter);
								}
							}}
						/>
					): null
				}

				{!disableSort ? <IntegratedSorting /> : null}
				
				{!disableSelect  || !showSelectionColumn? (
					<SelectionState
						selection={selection}
						onSelectionChange={handleSelectionChange}
					/>
				) : null}
				{!disableSelect ? <IntegratedSelection /> : null}

				{!disableFilter ? <FilteringState defaultFilters={[]}
									filters={props.defaultFilter !== undefined ? props.defaultFilter : []}
									columnFilteringEnabled={props.columnFilteringEnabled}
									columnExtensions={props.filterExtensions}
									onFiltersChange={props.onFilterChange}
								/> : null}

				{!disableFilter ? <IntegratedFiltering /> : null}


				<Table columnExtensions={columnExtensions} messages={{noData:props.message}}/>

				{!disableSelect  && !showSelectionColumn ? (
					<TableSelection
						showSelectAll={!singleSelect ? true : false}
						cellComponent={!singleSelect ? TableCheckBox : TableRadio}
						headerCellComponent={TableCheckBox}
					/>
				) : !showSelectionColumn ? (
					<TableSelection
						selectByRowClick
						showSelectionColumn={false}
						cellComponent={!singleSelect ? TableCheckBox : TableRadio}
						headerCellComponent={TableCheckBox}
					/>
				):null}

				<TableHeaderRow
					showSortingControls={!disableSort}
					sortLabelComponent={TableSortLabel}
				/>
				{!disableFilter ? <TableFilterRow cellComponent={TableFilterCell} /> : null}
				{columnBands ? <TableBandHeader columnBands={columnBands} /> : null}

				{!disableSearch || toolbarType ? <Toolbar rootComponent={toolbar} /> : null}
				{!disableSearch ? <SearchPanel inputComponent={TableSearchPanel} /> : null}

				{!disablePaging ? <PagingPanel pageSizes={pageSizes} /> : null}
			</Grid>
		</Paper>
	);
};

TableComponent.defaultProps = {
	rows: [],
	columns: [],
	columnExtensions: [],
	filterExtensions: [],
	columnBands: null,
	formattedColumns: {},
	singleSelect: false,
	disableSearch: false,
	disablePaging: false,
	disableSelect: false,
	disableFilter: false,
	disableSort: false,
	toolbarType: null,
	defaultFilter: [],
	showSelectionColumn: false,
	columnFilteringEnabled: true,
	id: "table-1",
	message: "No available data"
};
