import React, { useState } from 'react';
import { InputLabel, Select, FormControl, 
    OutlinedInput, MenuItem, FormHelperText } from '@material-ui/core/';
import { makeStyles} from '@material-ui/core/styles';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faPlus,
} from '@fortawesome/free-solid-svg-icons';

import { Components } from '@hims/core';

const SelectStyle = makeStyles({
    root: {
        borderTop: '0.5px solid rgba(60, 57, 74, 0.3)',
        color: '#0d5e40',
        fontSize: '14px',
        textDecoration: 'underline'
    },
    errorText:{
        marginTop: '5px',
        color: "#E53935"
    }
});

interface AddSelectProps {
    options: any;
    value: any;
    label: string;
    id: string;
    addButton?: boolean;
    addButtonLabel: string;
    disabled?: boolean;
    onChange: (value: any) => void;
    error: boolean;
    error_message: string;

}

type ISelectItem = Components.UI.InputSelect.ISelectItem;

export const AddSelect: React.FC<AddSelectProps> = (
	props: AddSelectProps,
): JSX.Element => {
    const classes = SelectStyle();

    const [currentValue, setCurrentValue] = useState<ISelectItem>(
        props.value
    );

    function onChange(event: React.ChangeEvent<{name?: string | undefined, value: any}>){
        if(props.addButton && event.target.value === "add"){
            const item = {
                id:0,
                value: "add",
                label: "Add"
            }
            if(props.onChange){
                props.onChange(item);
            }
        }else{
            const selectedItem =
                props.options.find(item => item.value === event.target.value)
            setCurrentValue(selectedItem);
            if(props.onChange){
                props.onChange(selectedItem);
            }
        }
    }
    
    return (
        <div>
            <InputLabel htmlFor="data-map-select">{props.label}</InputLabel>
            <FormControl margin="normal" style={{width: '100%'}} error={props.error}>
                <Select
                    disabled={props.disabled}
                    id={props.id}
                    value={currentValue.value}
                    input={<OutlinedInput labelWidth={0} name="data-map-select" id="data-map-select" />}
                    onChange={onChange}
                >
                    { props.options.map( data => (
                        <MenuItem key={data.id} value={data.value}>{data.label}</MenuItem>
                    ))}
                    { props.addButton ?
                        (<MenuItem id={props.id + "_add_btn"} value={"add"} className={classes.root}>
                            <FontAwesomeIcon icon={faPlus} style={{ color: '#0D5E40', marginRight: '10px' }} /> {props.addButtonLabel}
                        </MenuItem>):null
                    }
                </Select>
                { props.error === true ?
                    (<FormHelperText 
                        id={props.id+"_error"}
                        className={classes.errorText}
                    >
                        {props.error_message}
                    </FormHelperText>):null
                }
            </FormControl>
        </div>
    )
}

AddSelect.defaultProps = {
    disabled: false,
    error: false,
    error_message: ""
}