import * as React from 'react';
import { Grid, TextField, InputLabel,
    FormHelperText } from '@material-ui/core/';
import { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';

import clsx from 'clsx';

import { Components } from '@hims/core';
import { DatePicker } from 'Components/UI/DatePicker';
import { AddSelect } from 'Components/UI/AddSelect';
import { AddNewBatchModal } from '../AddNewBatch';

interface AddDetailstProps {
    state_data: any;
    responseData: any;
    client_id: any;
    details_data: any;
    contracts_data: any;
    updateDetails: (value: Object, attr: String) => void;
    updateState: (data: any, attr:string) => void;

}

const useStyles = makeStyles({
    label: {
      fontSize:'1rem'
    },
    errorText:{
        marginTop: '5px',
        color: "#E53935"
    },
    errorText2:{
        color: "#E53935",
        marginTop: '0px',
    }
  });

type ISelectItem = Components.UI.InputSelect.ISelectItem;

export const AddDetails: React.FC<AddDetailstProps> = (
	props: AddDetailstProps,
): JSX.Element => {
    const [ accounts] = useState<any>(function(){
        const accounts_data: any[] = props.responseData.map(item => {
			return {
				id: item["_id"],
				value: item["_id"],
				label: item.registered_name
			};
        });
        return accounts_data;
    });
    const [ mediums ] = useState([
        {
            id: 1,
            value: "Email",
            label: "Email"
        },
        {
            id: 2,
            value: "Fax",
            label: "Fax"
        },
        {
            id: 3,
            value: "Client Portal",
            label: "Client Portal"
        }
    ]);
    const [ selectedAccount, setSelectedAccount] = useState(props.details_data["account"]);
    const [ senderName, setSenderName] = useState(props.details_data["sender_name"]);
    const [ senderEmail, setSenderEmail] = useState(props.details_data["sender_email"]);
    const [ selectedDate, setSelectedDate] = useState(props.details_data["date_sent"]);
    const [ selectedNumberMember, setNumberMember] = useState(props.details_data["number_of_members"]);
    const [ openBatchModal, setOpenBatchModal] = useState(false);
    const [ contracts, setContract ] = useState<any>([]);
    const [ batchnames, setBatchNames] = useState<any>([]);

    const classes = useStyles();

    function getContracts(id: any){
        if(id && props.contracts_data){
            const data_item = props.contracts_data.find(data => data["_id"] === id);
            if(data_item && data_item["contract_objects"]){
                /******* need to remove duplicate contracts *****/
                let contract_ids: any = [];
                let contract_items:any = [];
                for(const cnt in data_item["contract_objects"]){
                    const temp_contract = data_item["contract_objects"][cnt];
                    if(contract_ids.indexOf(temp_contract["_id"]) === -1){
                        contract_items.push({
                            id: temp_contract["_id"],
                            value: temp_contract["_id"],
                            label: temp_contract.name
                        })
                        contract_ids.push(temp_contract["_id"])
                    }
                }
                // const contract_items: any[] = data_item["contract_objects"].map(item => {
                //     return {
                //         id: item["_id"],
                //         value: item["_id"],
                //         label: item.name
                //     };
                // });
                return contract_items;
            }else{
                return [];
            }
        }else{
            return [];
        }
    }

    function getBatchNames(id: any, account_id: any){
        if(id && account_id && props.contracts_data){
            let batch_items: any = [];
            for(const item_cnt in props.contracts_data){
                const contract = props.contracts_data[item_cnt];
                if(contract["_id"] === account_id){
                    for(var obj_cnt in contract["upload_batch_objects"]){
                        const batch = contract["upload_batch_objects"][obj_cnt];
                        if(batch["contract_id"] === id){
                            batch_items.push({
                                id: batch["batch_name"],
                                value: batch["batch_name"],
                                label: batch["batch_name"]
                            });
                        }
                    }
                    break;
                }
            }
            return batch_items;
        }else{
            return [];
        }
    }

    useEffect(()=>{
        const contract_items = getContracts(props.client_id);
        setContract(contract_items)
    }, [props.contracts_data])

    function getAccountInformation(data_item: Object, initial_load: Boolean){
        const contract_items = getContracts(data_item["id"]);
        setContract(contract_items)
        if(initial_load && contract_items.length === 1){
            props.updateDetails(contract_items[0], "contract")
        }

        if(data_item["member_data_fields"] !== undefined){
            props.updateState(data_item["member_data_fields"], "system_names");
        }
    }

    function handleChangeAccount(item: ISelectItem){
        setSelectedAccount(item);
        const details = props.details_data;
        details["account"] = item;
        props.updateDetails(details, "account");
        if(item.id){
            const data_item = props.responseData.find(data => data["_id"] === item["id"]);
            getAccountInformation(data_item, false);
        }
    }

    function handleContract(item: ISelectItem){
        const details = props.details_data;
        details["contract"] = item;
        props.updateDetails(details, "contract");
        if(item.id){
            const batch_items = getBatchNames(item.id, props.details_data["account"]["value"]);
            setBatchNames(batch_items)
        }
    }

    function handleMedium(item: ISelectItem){
        const details = props.details_data;
        details["sent_through_channel"] = item;
        props.updateDetails(details, "sent_through_channel");
    }

    function handleBatch(item: ISelectItem){
        if(item.value !== "add"){
            const details = props.details_data;
            details["batch_name"] = item;
            props.updateDetails(details, "batch_name");
        }else{
            setOpenBatchModal(true);
        }
    }

    function handleSenderEmail(e: React.ChangeEvent<HTMLInputElement>){
        const { value } = e.target;
        setSenderEmail(value);
        const details = props.details_data;
        details["sender_email"] = value;
        props.updateDetails(details, "sender_email");
    }

    function handleSenderName(e: React.ChangeEvent<HTMLInputElement>){
        const { value } = e.target;
        setSenderName(value);
        const details = props.details_data;
        details["sender_name"] = value;
        props.updateDetails(details, "sender_name");
    }

    function handleNumberMember(e: React.ChangeEvent<HTMLInputElement>){
        const { value } = e.target;
        setNumberMember(value)
        const details = props.details_data;
        details["number_of_members"] = value;
        props.updateDetails(details, "number_of_members");
    }
    
    function handleDateSent(date: Date){
        setSelectedDate(date)
        const details = props.details_data;
        details["date_sent"] = date;
        props.updateDetails(details, "date_sent");
    }

    function closeBatchNameModal(){
        setOpenBatchModal(false);
    }

    function handleAddBatchName(name: String){
        /*check batch name if exist in list*/
        const new_batch = {
            id: "",
            value: name,
            label: name
        }
        const batches = batchnames;
        batches.push(new_batch);
        setBatchNames(batches);
        setOpenBatchModal(false);
        // const details = props.details_data;
        // details["batch_name"] = new_batch;
        // props.updateDetails(details);
    }

    return (
        <Grid 
            container
            alignItems="stretch"
            className={clsx('member-body')}
        >
            <Grid item xs={12}>
                <Grid
                    item 
                    container
                    spacing={4}
                >
                    <Grid item xs={4}>
                        <AddSelect
                            id={"add_details_account"}
                            options={accounts}
                            disabled={props.client_id ? true: false}
                            label={"Corporate Account"}
                            value={selectedAccount}
                            error={props.state_data.account_error}
                            error_message={props.state_data.account_error_message}
                            addButton={false}
                            addButtonLabel={""}
                            onChange={handleChangeAccount}
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <AddSelect
                            id={"add_details_contract"}
                            options={contracts}
                            label={"Contract"}
                            disabled={props.details_data["account"]["value"] ? false: true}
                            value={props.details_data["contract"]}
                            error={props.state_data.contract_error}
                            error_message={props.state_data.contract_error_message}
                            addButton={false}
                            addButtonLabel={""}
                            onChange={handleContract}
                        />
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12} className={clsx('member-body')}>
                <Grid
                    item 
                    container
                    spacing={4}
                >
                    <Grid item xs={4}>
                        <InputLabel
                            className={classes.label}
                            htmlFor="add_details_sender_name">
                            Sender Name
                        </InputLabel>
                        <TextField
                            style={{marginTop:'8px', width: '100%'}}
                            id="add_details_sender_name"
                            margin="normal"
                            variant="outlined"
                            value={senderName}
                            onChange={handleSenderName}
                            error={props.state_data.sender_name_error}
                            inputProps={{ 'aria-label': 'bare' }}
                        />
                        { props.state_data.sender_name_error === true ?
                            (<FormHelperText 
                                id="add_details_sender_name_error"
                                className={classes.errorText}
                            >
                                {props.state_data.sender_name_error_message}
                            </FormHelperText>):null
                        }
                    </Grid>
                    <Grid item xs={4}>
                        <InputLabel
                            className={classes.label}
                            htmlFor="add_details_sender_email">
                            Sender Email
                        </InputLabel>
                        <TextField
                            style={{marginTop:'8px', width: '100%'}}
                            id="add_details_sender_email"
                            type="email"
                            margin="normal"
                            variant="outlined"
                            value={senderEmail}
                            error={props.state_data.sender_email_error}
                            onChange={handleSenderEmail}
                            inputProps={{ 'aria-label': 'bare' }}
                        />
                        { props.state_data.sender_email_error === true ?
                            (<FormHelperText 
                                id="add_details_sender_email_error"
                                className={classes.errorText}
                            >
                                {props.state_data.sender_email_error_message}
                            </FormHelperText>):null
                        }
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12} className={clsx('member-body')}>
                <Grid
                    item 
                    container
                    spacing={4}
                >
                    <Grid item xs={4}>
                        <AddSelect
                            id={"add_details_sent_through_channel"}
                            options={mediums}
                            label={"Sent through channel"}
                            value={props.details_data["sent_through_channel"]}
                            error={props.state_data.sent_through_channel_error}
                            error_message={props.state_data.sent_through_channel_error_message}
                            addButton={false}
                            addButtonLabel={""}
                            onChange={handleMedium}
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <DatePicker
                            id={"add_details_date_sent"}
                            placeholder='Date Sent'
                            value={selectedDate}
                            error={props.state_data.date_sent_error}
                            error_message={props.state_data.date_sent_error_message}
                            onChange={handleDateSent}/>

                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12} className={clsx('member-body')}>
                <Grid
                    item 
                    container
                    spacing={4}
                >
                    <Grid item xs={3}>
                        <AddSelect
                            id={"add_details_batch_name"}
                            disabled={props.details_data["contract"]["value"] ? false: true}
                            options={batchnames}
                            label={"Batch Name"}
                            value={props.details_data["batch_name"]}
                            addButton={true}
                            addButtonLabel={"New Batch"}
                            onChange={handleBatch}
                            error={props.state_data.batch_name_error}
                            error_message={props.state_data.batch_name_error_message}
                        />
                    </Grid>
                    <Grid item xs={3}>
                        <InputLabel
                            className={classes.label}
                            htmlFor="add_details_number_members">
                            No. of Members
                        </InputLabel>
                        <TextField
                            style={{marginTop:'8px'}}
                            id="add_details_number_members"
                            type="number"
                            margin="normal"
                            variant="outlined"
                            value={selectedNumberMember}
                            onChange={handleNumberMember}
                            error={props.state_data.number_of_members_error}
                            inputProps={{ 'aria-label': 'bare' }}
                        />
                        { props.state_data.number_of_members_error === true ?
                            (<FormHelperText 
                                id="add_details_number_of_members_error"
                                className={classes.errorText2}
                            >
                                {props.state_data.number_of_members_error_message}
                            </FormHelperText>):null
                        }
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12}>
                <AddNewBatchModal
                    id={"add_new_batch_modal"}
                    batchnames={batchnames}
                    isModalOpen={openBatchModal}
                    onClose={closeBatchNameModal}
                    onAdd={handleAddBatchName}
                />
            </Grid>
        </Grid>
    )
}

AddDetails.defaultProps = {};