
let Processmaker_URL: string;
if(process.env.NODE_ENV === "development"){
    Processmaker_URL = "http://localhost:32766/api/1.0/workflow";
}else{
    Processmaker_URL = "http://18.140.39.215/api/1.0/workflow";
}

// const PROCESSMAKER_TOKEN = "db649586ddbe061e9e4a7c8320ed0948ffccc4a4";
const PROCESSMAKER_TOKEN = localStorage.getItem('pm_token');

export class Processmaker {
    static get(url:string){
        return fetch(Processmaker_URL+url, {
		   	method: 'GET',
		   	headers: { 
                'Content-Type': 'application/json',
                'Authorization' : 'Bearer '+ PROCESSMAKER_TOKEN
            },
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static post(url:string,body:any){
        return fetch(Processmaker_URL+url, {
		   	method: 'POST',
		   	headers: { 
                'Content-Type': 'application/json',
                'Authorization' : 'Bearer '+ PROCESSMAKER_TOKEN
            },
            body: JSON.stringify(body)
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static put(url:string,body:any){
        return fetch(Processmaker_URL+url, {
		   	method: 'PUT',
		   	headers: { 
                'Content-Type': 'application/json',
                'Authorization' : 'Bearer '+ PROCESSMAKER_TOKEN
            },
            body: JSON.stringify(body)
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }
}