import './style.scss';

//#region Interface Imports
import { Store } from 'Components/Stores/IStore';
import { IMemberPage } from './IMemberPage';
import { MemberActions } from './MemberActions';
import { Loader } from 'Components/UI/LoadingIndicator';
import { PageHeaderComponent } from 'Components/UI/PageHeader';
import { TableComponent } from 'Components/UI/TableComponent';
// import { AddSelect } from 'Components/UI/AddSelect';
import { Components } from '@hims/core';
import { GlobalFunction } from 'Components/UI/GlobalFunction';
import { API } from '../API';
// import { DateRange } from 'Components/UI/DateRange';

type ISelectItem = Components.UI.InputSelect.ISelectItem;
//#endregion Interface Imports

export class MemberPage extends React.Component<IMemberPage.IProps, IMemberPage.IState> {
	constructor(props: IMemberPage.IProps) {
        super(props);
        const table : any = {
            formattedColumns:{},
            columnExtensions:[
                {
                    columnName: "member_card_no",
                    width: 200,
                    wordWrapEnabled: true
                },
                {
                    columnName: "member_name",
                    width: 250,
                    wordWrapEnabled: true
                },
                {
                    columnName: "corporates",
                    width: 200,
                    wordWrapEnabled: true
                },
                {
                    columnName: "effectivity_date",
                    wordWrapEnabled: true
                },
                {
                    columnName: "termination_date",
                    wordWrapEnabled: true
                },
                {
                    columnName: "status",
                    wordWrapEnabled: true
                }
            ],
            columns: [
                {
                    name: "member_card_no",
                    title: "Member ID"
                },
                {
                    name: "member_name",
                    title: "Name"
                },
                {
                    name: "corporates",
                    title: "Corporate/s"
                },
                {
                    name: "effectivity_date",
                    title: "Effective Date"
                },
                {
                    name: "termination_date",
                    title: "Termination Date"
                },
                {
                    name: "status",
                    title: "Status"
                }
            ],
            rows: []
        }
		this.state={
            loading_state: true,
            member_list: [],
            member_data: table,
            member_data_all: table,
            selected_date_filter:{
                id: "",
                value: "This Month",
                label: "This Month"
            },
            selected_start_date: new Date(),
            selected_end_date: new Date(),
            global_filter: "",
            redirect_to_member: false,
            member_id: "",
            default_filters:[]
        }
        this.handleChangeFilter = this.handleChangeFilter.bind(this);
        this.handleDateSent = this.handleDateSent.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.handleClickRow = this.handleClickRow.bind(this);
        this.handleFilterChange = this.handleFilterChange.bind(this);
        this.handleChangeSorting = this.handleChangeSorting.bind(this);
	}

    /******* get members info when loading the page *******/
	componentWillMount(){
        API.getMembers().then(response => {
            if(response && response.error === undefined){
                // const array = response.concat(response);
                const table = this.generateDataForTable(response);
                this.setState({
                    member_list: response,
                    member_data_all: table,
                    loading_state: false
                })
            }else{
                this.setState({
                    member_list: [],
                    member_data_all: {},
                    loading_state: false
                })
            }
        });
    }

    /******* generate data for table *******/
    generateDataForTable(response: any){
        const table : any = {
            formattedColumns:{},
            columnExtensions:[
                {
                    columnName: "member_card_no",
                    width: 200,
                    wordWrapEnabled: true
                },
                {
                    columnName: "member_name",
                    width: 250,
                    wordWrapEnabled: true
                },
                {
                    columnName: "corporates",
                    width: 200,
                    wordWrapEnabled: true
                },
                {
                    columnName: "effectivity_date",
                    wordWrapEnabled: true
                },
                {
                    columnName: "termination_date",
                    wordWrapEnabled: true
                },
                {
                    columnName: "status",
                    wordWrapEnabled: true
                }
            ],
            columns: [
                {
                    name: "member_card_no",
                    title: "Member ID"
                },
                {
                    name: "member_name",
                    title: "Name"
                },
                {
                    name: "corporates",
                    title: "Corporate/s"
                },
                {
                    name: "effectivity_date",
                    title: "Effective Date"
                },
                {
                    name: "termination_date",
                    title: "Termination Date"
                },
                {
                    name: "status",
                    title: "Status"
                }
            ],
            rows: []
        }
        for(const cnt in response){
            const item = response[cnt];
            const temp_row: any = {};
            temp_row["member_card_no"] = item["member_card_no"];
            if(item["effectivity_date"] !== undefined && item["effectivity_date"] !== null){
                temp_row["effectivity_date"] = GlobalFunction.toCommonDateString(new Date(item["effectivity_date"]));
            }else{
                temp_row["effectivity_date"] = "";
            }
            if(item["termination_date"] !== undefined && item["termination_date"] !== null){
                temp_row["termination_date"] = GlobalFunction.toCommonDateString(new Date(item["termination_date"]));
            }else{
                temp_row["termination_date"] = "";
            }
            let member_name: string = "";
            if(item["first_name"] && item["first_name"] !== undefined && item["first_name"] !== null){
                member_name += item["first_name"];
            }
            if(item["middle_name"] && item["middle_name"] !== undefined && item["middle_name"] !== null){
                member_name += " " + item["middle_name"].substr(0, 1) + ".";
            }
            if(item["last_name"] && item["last_name"] !== undefined && item["last_name"] !== null){
                member_name += " " + item["last_name"];
            }
            if(item["client_object"] && item["client_object"] !== undefined && item["client_object"] !== null){
                const corporates: any[] = item["client_object"].map( item =>{
                    return item["registered_name"];
                })
                temp_row["corporates"] = corporates.join(", ");
            }else{
                temp_row["corporates"] = "";
            }
            if(item["member_status"] && item["member_status"] !== undefined && item["member_status"] !== null){
                temp_row["status"] = item["member_status"];
            }else{
                temp_row["status"] = "";
            }
            temp_row["member_name"] = member_name;
            temp_row["id"] = item["_id"];
            table["rows"].push(temp_row);
        }
        return table;
    }
    
    /******* handles click on row in the table *******/
    handleClickRow(row: any){
        const { member_data } = this.state;
        const member_id = member_data["rows"][row[0]]["id"];
        if(member_id){
            this.setState({
                redirect_to_member: true,
                member_id: member_id
            })
        }
    }

    /******* handles changes in month filter *******/
    handleChangeFilter(item: ISelectItem){
        console.log(" item >>>", item);
        this.setState({
            selected_date_filter: item
        })
    }

    /******* handles changes in date range *******/
    handleDateSent(date: Date, attr: string){
        if(attr === "start"){
            this.setState({
                selected_start_date: date
            })
        }else{
            this.setState({
                selected_end_date: date
            })
        }
    }

    /******* handles changes in global search *******/
    handleSearch(e: React.ChangeEvent<HTMLInputElement>){
        const { value } = e.target;
        this.setState({
            global_filter: value
        }, () => this.handleFilterChange([]));
        
    }

    /******* handles filters in table columns *******/
    handleFilterChange = (filter: any) => {
        const {
            member_data_all,
            member_data,
            global_filter
        } = this.state;
        let data = Object.assign({},member_data);
        let filtered: any[] = [];
        let filter_member_id: string = "";
        let filter_member_name: string = "";
        let filter_member_corporate: string = "";
        let filter_member_effectivity_date: string = "";
        let filter_member_termination_date: string = "";
        let filter_member_date_status: string = "";

        filter.forEach(item => {
            if(item.columnName === "member_card_no") filter_member_id = item.value.toLowerCase();
            if(item.columnName === "member_name") filter_member_name = item.value.toLowerCase();
            if(item.columnName === "corporates") filter_member_corporate = item.value.toLowerCase();
            if(item.columnName === "effectivity_date") filter_member_effectivity_date = item.value.toLowerCase();
            if(item.columnName === "termination_date") filter_member_termination_date = item.value.toLowerCase();
            if(item.columnName === "status") filter_member_date_status = item.value.toLowerCase();
        });

        if(filter.length > 0 && global_filter !== "" && global_filter !== undefined && global_filter !== null){
            member_data_all.rows.forEach(item => {
                if((item.member_card_no &&  item.member_card_no !== undefined && item.member_card_no !== null 
                    && item.member_card_no.toLowerCase().includes(global_filter.toLowerCase()))
                || (item.member_name && item.member_name !== undefined && item.member_name !== null 
                    && item.member_name.toLowerCase().includes(global_filter.toLowerCase()))
                || (item.corporates && item.corporates !== undefined && item.corporates !== null
                    && item.corporates.toLowerCase().includes(global_filter.toLowerCase()))
                || (item.effectivity_date && item.effectivity_date !== undefined && item.effectivity_date !== null
                    && item.effectivity_date.toLowerCase().includes(global_filter.toLowerCase()))
                || (item.termination_date && item.termination_date !== undefined && item.termination_date !== null
                    && item.termination_date.toLowerCase().includes(global_filter.toLowerCase()))
                || (item.status && item.status !== undefined && item.status !== null && item.status.toLowerCase().includes(global_filter.toLowerCase())))
                    if(item.member_card_no.toLowerCase().includes(filter_member_id)
                    && item.member_name.toLowerCase().includes(filter_member_name)
                    && item.corporates.toLowerCase().includes(filter_member_corporate)
                    && item.effectivity_date.toLowerCase().includes(filter_member_effectivity_date)
                    && item.termination_date.toLowerCase().includes(filter_member_termination_date)
                    && item.status.toLowerCase().includes(filter_member_date_status))
                        filtered.push(item);
            });
            data.rows = filtered;
            this.setState({
                member_data: data,
                default_filters: filter
            })
        }else if(global_filter !== "" && global_filter !== undefined && global_filter !== null){
            member_data_all.rows.forEach(item => {
                if((item.member_card_no && item.member_card_no !== undefined && item.member_card_no !== null
                    && item.member_card_no.toLowerCase().includes(global_filter.toLowerCase()))
                || (item.member_name && item.member_name !== undefined && item.member_name !== null 
                    && item.member_name.toLowerCase().includes(global_filter.toLowerCase()))
                || (item.corporates && item.corporates !== undefined && item.corporates !== null 
                    && item.corporates.toLowerCase().includes(global_filter.toLowerCase()))
                || (item.effectivity_date && item.effectivity_date !== undefined && item.effectivity_date !== null 
                    && item.effectivity_date.toLowerCase().includes(global_filter.toLowerCase()))
                || (item.termination_date && item.termination_date !== undefined && item.termination_date !== null 
                    && item.termination_date.toLowerCase().includes(global_filter.toLowerCase()))
                || (item.status && item.status !== undefined && item.status !== null && item.status.toLowerCase().includes(global_filter.toLowerCase())))
                    filtered.push(item);
            });
            data.rows = filtered;
            this.setState({
                member_data: data,
                default_filters: filter
            })
        } else if(filter.length === 0 || global_filter === "" || global_filter === undefined || global_filter === null){
            data.rows = [];
            this.setState({
                member_data: data,
                default_filters: filter
            })
        }
    }

    /******* handles sorting in table columns *******/
    handleChangeSorting(sorting: any){
        const {
            member_data,
            member_data_all
        } = this.state;
        let data = Object.assign({},member_data);
        let all = Object.assign({},member_data_all);
        console.log('sorting >>>>', sorting);

        if(sorting && sorting[0] && sorting[0]["columnName"]){
            switch(sorting[0]["columnName"]){
                case"member_card_no":
                    if(sorting[0]["direction"] === "asc"){
                        data.rows.sort((a,b)=> (a.member_card_no > b.member_card_no) ? 1 : -1);
                        all.rows.sort((a,b)=> (a.member_card_no > b.member_card_no) ? 1 : -1);
                    }else{
                        data.rows.sort((a,b)=> (a.member_name > b.member_name) ? -1 : 1);
                        all.rows.sort((a,b)=> (a.member_name > b.member_name) ? -1 : 1);
                    }
                break;
                case"member_name":
                    if(sorting[0]["direction"] === "asc"){
                        data.rows.sort((a,b)=> (a.member_name > b.member_name) ? 1 : -1);
                        all.rows.sort((a,b)=> (a.member_name > b.member_name) ? 1 : -1);
                    }else{
                        data.rows.sort((a,b)=> (a.member_name > b.member_name) ? -1 : 1);
                        all.rows.sort((a,b)=> (a.member_name > b.member_name) ? -1 : 1);
                    }
                break;
                case"corporates":
                    if(sorting[0]["direction"] === "asc"){
                        data.rows.sort((a,b)=> (a.corporates > b.corporates) ? 1 : -1);
                        all.rows.sort((a,b)=> (a.corporates > b.corporates) ? 1 : -1);
                    }else{
                        data.rows.sort((a,b)=> (a.corporates > b.corporates) ? -1 : 1);
                        all.rows.sort((a,b)=> (a.corporates > b.corporates) ? -1 : 1);
                    }
                break;
                case"effectivity_date":
                    if(sorting[0]["direction"] === "asc"){
                        data.rows.sort((a,b)=> (a.effectivity_date > b.effectivity_date) ? 1 : -1);
                        all.rows.sort((a,b)=> (a.effectivity_date > b.effectivity_date) ? 1 : -1);
                    }else{
                        data.rows.sort((a,b)=> (a.effectivity_date > b.effectivity_date) ? -1 : 1);
                        all.rows.sort((a,b)=> (a.effectivity_date > b.effectivity_date) ? -1 : 1);
                    }
                break;
                case"termination_date":
                    if(sorting[0]["direction"] === "asc"){
                        data.rows.sort((a,b)=> (a.termination_date > b.termination_date) ? 1 : -1);
                        all.rows.sort((a,b)=> (a.termination_date > b.termination_date) ? 1 : -1);
                    }else{
                        data.rows.sort((a,b)=> (a.termination_date > b.termination_date) ? -1 : 1);
                        all.rows.sort((a,b)=> (a.termination_date > b.termination_date) ? -1 : 1);
                    }
                break;
                case"status":
                    if(sorting[0]["direction"] === "asc"){
                        data.rows.sort((a,b)=> (a.status > b.status) ? 1 : -1);
                        all.rows.sort((a,b)=> (a.status > b.status) ? 1 : -1);
                    }else{
                        data.rows.sort((a,b)=> (a.status > b.status) ? -1 : 1);
                        all.rows.sort((a,b)=> (a.status > b.status) ? -1 : 1);
                    }
                break;
            }
            this.setState({
                member_data: data,
                member_data_all: all
            })
        }
    }



	public render(): JSX.Element {
        const {
            loading_state,
            member_data,
            global_filter,
            redirect_to_member,
            member_id,
            default_filters,
            // selected_date_filter,
            // selected_start_date,
            // selected_end_date
        } = this.state;
        // const date_options: any = [
        //     {
        //         id: "",
        //         value: "This Week",
        //         label: "This Week"
        //     },
        //     {
        //         id: "",
        //         value: "This Month",
        //         label: "This Month"
        //     },
        //     {
        //         id: "",
        //         value: "This Year",
        //         label: "This Year"
        //     },
        //     {
        //         id: "",
        //         value: "Custom Date Range",
        //         label: "Custom Date Range"
        //     }
        // ]
        if(redirect_to_member === true){
            return <Redirect to={{pathname: '/membership/view-member/'+member_id, state: { from: this.props.location }}}/>
        }else{
            return (
                <div className={clsx('MemberPage')}>
                    { loading_state ?
                        (<Loader/>) : null
                    }
                    <Grid
                        container
                        className={clsx('member-header')}
                    >
                        <Grid item xs={12}>
                            <PageHeaderComponent
                                id={"member_page_stepper"}
                                label="Members"
                                steps={[]}
                                activeStep={0}
                            />
                        </Grid>
                    </Grid>
                    <Grid
                        container
                        alignItems="stretch"
                    >
                        <Grid item xs={12}>
                            <Grid container>
                                {/* <Grid item xs={8}> */}
                                <Grid item xs={12}>
                                    <TextField
                                        style={{width: '100%'}}
                                        id="members_search"
                                        placeholder="Search members by typing their member ID, name, etc."
                                        label=""
                                        margin="normal"
                                        variant="outlined"
                                        value={global_filter}
                                        onChange={this.handleSearch}
                                        InputProps={{
                                            startAdornment: (
                                                <InputAdornment position="start">
                                                    <Search style={{color: 'rgba(54, 54, 54, 0.4)'}}/>
                                                </InputAdornment>
                                            ),
                                        }}
                                    />
                                </Grid>
                                {/*********** hide filter for now - not included in UAT */}
                                {/* <Grid item xs={4} style={{paddingLeft: '16px', paddingRight: '5px'}}>
                                    <Grid container direction="row">
                                        <Grid item xs={12}>
                                            <AddSelect
                                                id={"member_date_filter"}
                                                label={""}
                                                options={date_options}
                                                value={selected_date_filter}
                                                addButton={false}
                                                addButtonLabel={""}
                                                error={false}
                                                error_message={""}
                                                onChange={this.handleChangeFilter}
                                            />
                                        </Grid>
                                        { Object.keys(selected_date_filter).length > 0 && selected_date_filter.value === "Custom Date Range" ?
                                            <Grid item xs={12}>
                                                <DateRange
                                                    id={"members_custom_date"}
                                                    placeholder='Date Sent'
                                                    start={selected_start_date}
                                                    end={selected_end_date}
                                                    error={false}
                                                    error_message={""}
                                                    onChange={this.handleDateSent}/>
                                            </Grid>: null
                                        }
                                    </Grid>
                                </Grid> */}
                            </Grid>
                        </Grid>
                        <Grid item xs={12} style={{paddingTop:'32px'}}>
                            <TableComponent
                                id="tab-all"
                                rows={Object.keys(member_data).length > 0 ? member_data['rows']: []}
                                columns={Object.keys(member_data).length > 0 ? member_data['columns']: []}
                                message={global_filter ? 
                                    "No available data":
                                    "Start looking for members by typing in a member name, ID, or using the table filters." 
                                }
                                onClickRow={this.handleClickRow}
                                disableSelect
                                disableSearch
                                formattedColumns={Object.keys(member_data).length > 0 ? member_data['formattedColumns'] :{}}
                                columnExtensions={Object.keys(member_data).length > 0 ? member_data['columnExtensions'] : []}
                                onFilterChange={this.handleFilterChange}
                                onSortingChange={this.handleChangeSorting}
                                defaultFilter={default_filters}
                            />
                        </Grid>

                    </Grid>
                </div>
            );
        }
	}
}

const mapStateToProps = (state: Store) => state.home;

const mapDispatchToProps = (dispatch: Dispatch) => ({
	Map: bindActionCreators(MemberActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
