    /****** function that handles delete of file ******/
    function handleDeleteFile(){
        setUploadFileFlag(false);
        let temp_tables:any = {
            "rows":[],
            "columns":[],
            "formattedColumns": {}
        };
        let temp_columns: any = {
            columns: [
                {
                    name: "system_name",
                    title: "System Name"
                },
                {
                    name: "sheet_name",
                    title: "Sheet Name",
                    type: "select",
                    options:[
                        {
                            id :"",
                            value : "",
                            label : ""
                        }
                    ]
                }
            ],
            rows: []
        }
        const raw_data: any = {
            "columns": [],
            "rows": [],
            "data_values":{}
        };
        const filtered_raw_data: any = {};
        const tables: any = generateColumnsTable(temp_columns, raw_data, temp_tables, props.selected_data_map);
        temp_columns = tables["temp_table"];
        temp_tables = tables["temp_tables"];
        props.updateState(raw_data, "raw_uploaded_data");
        props.updateState(filtered_raw_data, "filtered_raw_data");
        props.updateState(temp_tables, "uploaded_data");
        props.updateState(temp_columns, "columns_data");
    }

