export const ApproveMemberModal: React.FC<ApproveMemberModalProps> = (
	props: ApproveMemberModalProps,
): JSX.Element => {
    const {
        isModalOpen,
        id,
        row,
    } = props;
    // const [tab, setSelectedTab] = useState(0);
    // const [partialcount, setPartialCount] = useState(0);
    // const [selectedpartial, setSelectedPartial] = useState<any>(data[0]);
    // const [status, setStatus] = useState(data[0]['status']);

    // console.log('data',data);

    // const [check1, setCheck1] = useState(false);
    // const [uploaded1] = useState(false);
    // const [selected1] = useState('');
    const [updata, setData] = useState({});
    const classes = useStyles();

    console.log('updata',updata);

    const handleSaveData = (data: any) => {
        setData(data);
        console.log('updata',updata);
    };

    const handleApprove = () => {
        props.onApprove(row,updata);
    };

    return (
        <Dialog
            id={id}
            maxWidth={false}
            open={isModalOpen}
            scroll={"paper"}
            aria-labelledby="modal-title"
            className={classes.dialog}
            style={{zIndex: 9999}}
        >
            <DialogTitle className={classes.dialogTitle} disableTypography={true} id="modal-title" >
                <div className={classes.dialogFirstTitle}>
                    Approve Member
                </div>
                <div className={classes.dialogSecondTitle}>
                    Check which document(s) to upload to support the approval of the member.
                </div>
                <IconButton aria-label="close" className={classes.closeButton} onClick={props.onClose}>
                    <CloseIcon />
                </IconButton>
            </DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <ModalContent 
                    isModalOpen={true}
                    onApprove={(data) => handleSaveData(data)}
                />
            </DialogContent>
            <DialogActions className={classes.dialogAction}>
                <Grid 
                    container
                    direction="row"
                    justify="space-evenly"
                    alignItems="stretch"
                >
                    <Grid item xs={4} style={{textAlign:'center'}}>
                        <Button
                            className={classes.cancelButton}
                            variant="contained"
                            color="secondary"
                            onClick={props.onClose}>
                            Cancel
                        </Button>
                    </Grid>
                    <Grid item xs={4} style={{textAlign:'center'}}>
                        <Button
                            className={classes.approveButton}
                            variant="contained"
                            color="primary"
                            onClick={handleApprove}>
                            Approve
                        </Button>
                    </Grid>
                </Grid>
            </DialogActions>   
        </Dialog>
    )
}

ApproveMemberModal.defaultProps = {
    isModalOpen: false,
    id: 'modal-id'
};
